import { createContext, useState } from "react";

export const ThemeContext=createContext()

export const ThemeContextProvider=({children})=>{
    const [theme,setTheme]=useState("rgb(190, 241, 241)")
    const handle=()=>{
      setTheme(theme==="rgb(190, 241, 241)"?"pink":"rgb(190, 241, 241)")
    }
    return <ThemeContext.Provider value={{theme,handle}}>{children}</ThemeContext.Provider>
}
