import { Card,Button } from 'antd';
import AOS from 'aos';
import 'aos/dist/aos.css';
AOS.init()
const { Meta } = Card;



export const Project=()=>{
  return <div id='pro' data-aos="zoom-out-up">
       <h1>PROJECTS</h1>
      <div id='project' >
     <div data-aos="fade-right">
   <Card
  hoverable
  style={{ width: 350 }}
  cover={<img alt="example" src="https://i.postimg.cc/j2457Ttf/Screenshot-383.png" />}
>
  <Meta title="CROMA" description="An eCommerce website to purchase electronic products and accessories. Tech Stack - EJS, React, Node.js, Express, MongoDB" />
  <Button style={{
      marginTop:"15px",
      marginRight:"50px"
  }} type="primary" ghost>
    <a href="https://cromaproject.herokuapp.com/"  target="_blank">See Live</a>
    </Button>
    <Button  type="primary" ghost>
    <a href="https://github.com/Amanfw13064/Croma-Backend"  target="_blank">GitHub</a>
    </Button></Card>
    </div>
    <div data-aos="fade-left">
    <Card
  hoverable
  style={{ width: 350 }}
  cover={<img alt="example" src="https://i.postimg.cc/PxzKV6Hj/Screenshot-494.png" />}
>
  <Meta title="Nike" description="An international E-commerce website popular for sports shoes, shirts,
trousers, etc. Tech Stack - HTML, CSS, JavaScript." />
  <Button style={{
      marginTop:"15px",
      marginRight:"50px"
  }} type="primary" ghost>
    <a href="https://nike-clone-unit2.netlify.app/"  target="_blank">See Live</a>
    </Button>
    <Button  type="primary" ghost>
    <a href="https://github.com/Sudhanshu894/nike-clone-project-"  target="_blank">GitHub</a>
    </Button></Card>
    </div>
</div>
</div>
}

